
.app-window {
  transition: 0.5s; }
.menu-open {
  height: 100vh;
  transform: translate(250px, -150px) rotateZ(5deg) scale(0.3, 0.3);
  &:hover {
    transform: translate(240px, -150px) rotateZ(-5deg) scale(0.3, 0.3);
    cursor: pointer; } }
.main_window {
  position: relative; }
.navbar {
  transition: 0.5s;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background: #fff;
  box-shadow: 5px 0 10px -10px #999;
  z-index: 1000;
  width: 80px; }
.navbar__burger-menu {
  background: #ff5e3a;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  button {
    cursor: pointer;
    border: none;
    background: none;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    outline: none;
    position: relative;
    background: #fff; }
  span {
    width: 30px;
    height: 2px;
    background: #000;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -1px;
    margin-left: -15px;
    &:before, &:after {
      content: "";
      display: block;
      width: 30px;
      height: 2px;
      background: #000;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -1px;
      margin-left: -15px;
      transition: 0.5s; }
    &:before {
      transform: translateY(-5px); }
    &:after {
      transform: translateY(5px); } } }

.navbar__burger-menu_active {
  span {
    &:before {
      width: 15px;
      transform: rotate(-35deg);
      transform-origin: left bottom; }
    &:after {
      width: 15px;
      transform-origin: left top;
      transform: rotate(35deg); } } }

.navbar__ref {
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      transition: 1s;
      transition-delay: 0.25s; } }
  img {
    width: 40px; } }

.navbar__uncover {
  width: 400px;
  .navbar__ref {
    a {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-left: 40px;
      img {
        margin-right: 20px; } } } }


.button-ref {
  margin: 70px 10px;
  padding: 10px;
  overflow: hidden;
  position: relative;
  transition: 1s;
  transition-delay: 0.25s;
  &:hover {
    box-shadow: 0 0 10px #9a9fbf, 0 0 40px #9a9fbf;
    background: #9a9fbf;
    a {
      span {
        color: #fff; } }
    .button-line_top {
      left: 100%;
      transition: 1s;
      transition-delay: 0.25s; }
    .button-line_bottom {
      left: -100%;
      transition: 1s;
      transition-delay: 0.25s; }
    .button-line_left {
      bottom: 100%;
      transition: 1s; }
    .button-line_right {
      top: 100%;
      transition: 1s; } } }
.button-line {
  position: absolute;
  display: block; }

.button-line_top {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg,transparent,#9a9fbf); }
.button-line_bottom {
  bottom: 0;
  left: 100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg,transparent,#9a9fbf); }

.button-line_left {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(0deg,transparent,#9a9fbf); }
.button-line_right {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg,transparent, #9a9fbf); }
@media screen and (max-width: 700px) {
  .navbar__cover {
    height: 70px; }
  .navbar__uncover {
    width: 100vw;
    height: 100vh; } }

@media screen and (max-height: 720px) {
  .button-ref {
    margin: 45px 10px; } }


.dark-theme {
  .navbar {
    background: #252525;
    box-shadow: 5px 0 10px -10px #000; }
  .button-ref {
    &:hover {
      box-shadow: 0 0 10px #ff5e3a, 0 0 40px #ff5e3a;
      background: #ff5e3a; } }
  .button-line {
    position: absolute;
    display: block; }

  .button-line_top {
    background: linear-gradient(90deg,transparent,#ff5e3a); }
  .button-line_bottom {
    background: linear-gradient(270deg,transparent,#ff5e3a); }
  .button-line_left {
    background: linear-gradient(0deg,transparent,#ff5e3a); }
  .button-line_right {
    background: linear-gradient(180deg,transparent, #ff5e3a); } }

