.paginator {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  color: #fff; }
.paginator__button {
  button {
    outline: none;
    transition: 1s;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    color: #000;
    border: none;
    cursor: pointer;
    margin-right: 20px;
    &:hover {
      background: #ccc; } } }
.paginator__active-page {
  button {
    background: #00BFFF;
    color: #fff; } }

@media screen and (max-width: 710px) {
  .paginator__button {
    button {
      font-size: 10px;
      width: 40px;
      height: 40px;
      margin-right: 5px; } } }

@media screen and (max-width: 350px) {
  .paginator__button {
    button {
      font-size: 10px;
      width: 30px;
      height: 30px;
      margin-right: 5px; } } }

.dark-theme {
  .paginator__button {
    button {
      background: #555;
      color: #fff; } }
  .paginator__active-page {
    button {
      background: #00BFFF; } } }
