.header-app {
  position: fixed;
  z-index: 1000;
  background: #3f4257;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center; }
.header-app__active_page {
  color: #fff;
  font-weight: 600;
  width: 83%;
  margin-left: 100px; }
.header-app__user-avatar {
  display: flex;
  align-items: center;
  img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: block; }
  p {
    color: #fff;
    margin-left: 10px;
    font-size: 16px; }
  button {
    background: none;
    border: none;
    color: #fff;
    cursor: pointer;
    margin-left: 30px;

    img {
      width: 32px;
      height: 32px; } } }
