
.home-page {
  padding: 200px 0; }
.scene {
  z-index: -1;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  min-height: 100vh; }


.layer {
  position: absolute;
  left: -5%;
  top: -30%;
  width: 115%;
  height: 140%; }

.layer-bg {
  background: url(../../img/nebo.jpg) center no-repeat;
  background-size: cover; }

.layer-up-bg {
  //background: url(https://i.ya-webdesign.com/images/mountain-peak-png-4.png) center no-repeat
  background: url(../../img/ррр3-removebg-preview.png) center no-repeat;
  //background: url(../../img/hg.png) center no-repeat
  background-size: cover; }

.layer-up {
  //background: url(https://clipart-best.com/img/mountain/mountain-clip-art-11.png) center no-repeat
  background: url(../../img/mount8.png) no-repeat;
  //background: url(../../img/ppp5-removebg-preview.png) no-repeat
  //background: url(../../img/ghfghf.png) no-repeat
  //background: url(../../img/ghfghf.png) no-repeat
  background-size: cover; }






.home-page__container {
  width: 50%;
  margin: 0 auto; }

.home-page__window-border {
  box-sizing: border-box;
  border: 10px solid #fff;
  border-image-slice: 100;
  border-image-repeat: repeat;
  background-clip: padding-box;
  border-image-source: url("../../img/pngfuel.com.png"); }

.home-page__window {
  width: 100%;
  box-sizing: border-box;
  color: #444;
  z-index: 1000;
  background: #fff;
  margin-bottom: 40px;
  &:before {
    background: #000; } }

.home-page__small-window {
  text-align: center;
  margin: 30px;
  background: #fff;
  box-sizing: border-box; }
.home-page__header {
  h1 {
    font-size: 90px;
    padding-bottom: 60px;
    border-bottom: 2px double #FF7400;
    margin-bottom: 60px; } }
.home-page__buttons {
  display: flex;
  justify-content: center;
  .submit-button {
    button {
      margin: 0 20px;
      padding: 20px 0;
      width: 200px;
      background: #FF7400; }
    &:nth-child(2) {
      button {
        background: #444; } } } }


.home-page__info-reg {
  width: 70%;
  margin: 0 auto 40px; }
.home-page__dignities {
  display: flex;
  justify-content: space-between; }

.home-page__dignitie {
  padding-right: 10px;
  width: 33%;
  margin-bottom: 70px;
  h3 {
    line-height: 1;
    font-size: 36px;
    word-break: break-all;
    margin-bottom: 7px; }
  &:last-child {
    border-right: none; }
  border-right: 5px dotted #FF7400; }


@media screen and (max-width: 1740px) {
  .layer {
    left: -5%;
    top: -5%;
    width: 115%;
    height: 115%; }
  .home-page__container {
    width: 70%; } }
@media screen and (max-width: 1260px) {
  .home-page__container {
    width: 90%; } }
@media screen and (max-width: 970px) {
  .home-page__container {
    width: 90%; }
  .home-page__dignities {
    flex-wrap: wrap; }
  .home-page__dignitie {
    width: 100%;
    margin-bottom: 30px;
    border: none; } }
@media screen and (max-width: 580px) {
  .home-page {
    padding: 50px 0; }
  .home-page__header {
    h1 {
      font-size: 60px; } }
  .home-page__buttons {
    .submit-button {
      button {
        margin: 0 10px;
        padding: 10px 0;
        width: 150px; } } } }
@media screen and (max-width: 400px) {
  .home-page__header {
    h1 {
      font-size: 46px; } }
  .home-page__buttons {
    .submit-button {
      button {
        margin: 0 5px;
        padding: 10px 0;
        width: 100px; } } }
  .home-page__dignitie {
    h3 {
      font-size: 26px; } } }
