.login__bg-header {
  padding-top: 70px;
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  align-items: center; }
.login__container {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center; }
.login__info {
  width: 50%;
  color: #fff;
  h1 {
    font-weight: 300;
    font-size: 44px;
    margin-bottom: 40px; }
  h2 {
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 40px; }
  a {
    cursor: pointer;
    background: transparent;
    border: 2px solid #fff;
    color: #fff;
    padding: 12px 15px;
    border-radius: 5px;
    outline: none; } }
.login__input-window {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  width: 42%;
  background: #fff;
  height: 650px;
  display: flex; }

.login__input-window-with-captcha {
  height: 820px; }

.login__switch-panel {
  width: 70px;
  button {
    transition: 0.6s;
    cursor: pointer;
    outline: none;
    border: none;
    background: #fff;
    width: 100%;
    height: 50%;
    border-right: 1px solid #e6ecf5;
    &:first-child {
      border-bottom: 1px solid #e6ecf5; }
    img {
      opacity: 0.3;
      width: 20px; }
    &:hover {
      background: #fafbfd; } } }

.login__input-fields {
  width: 100%; }
.login__window-title {
  h3 {
    box-sizing: border-box;
    width: 100%;
    font-size: 16px;
    font-weight: 500;
    padding: 20px 20px;
    border-bottom: 1px solid #e6ecf5; } }

.login__input-field, .login__additional-network {
  width: 80%;
  margin: 0 auto; }

.login__input-field {
  .submit-button {
    button {
      width: 100%;
      background: #ff5e3a;
      height: 50px;
      font-weight: 600; } } }
.login__field {
  margin-top: 30px; }
.field-normal {
  position: relative;
  input[type="text"] {
    transition: 0.2s;
    box-sizing: border-box;
    outline: none;
    padding: 20px 15px 0;
    width: 100%;
    height: 60px;
    border-radius: 5px;
    border: 1px solid #e6ecf5;
    font-size: 16px;
    border-bottom: 1px solid #999; }
  span {
    transition: 0.2s;
    position: absolute;
    top: 32%;
    left: 15px;
    z-index: 2;
    font-size: 16px;
    color: #999;
    pointer-events: none; }
  input[type="text"] {
    &:focus {
      + .label {
        color: #1E90FF;
        transform: translate(-10px, -15px) scale(0.75); }
      border-bottom: 2px solid #1E90FF;
      background: rgba(#000,.05); }
    &:not(:placeholder-shown) {
      + .label {
        transform: translate(-10px, -15px) scale(0.75); }
      border-bottom: 2px solid #32CD32; } } }


.login__field-checkbox {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  input {
    transition: 0.5s;
    box-sizing: border-box;
    cursor: pointer;
    margin-right: 10px;
    height: 20px;
    width: 30px;
    border: none;
    &:focus {
      border: 1px solid red; } }
  label {
    margin-bottom: 5px;
    color: #999; } }


.login__additional-network {
  .submit-button {
    button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 50px;
      font-weight: 600;
      img {
        margin-right: 10px;
        width: 30px; } }
    &:nth-child(2) {
      button {
        background: #2f5b9d;
        margin: 30px 0; } }
    &:nth-child(3) {
      button {
        background: #38bff1; } } } }

.login__registration {
  .login__input-field {
    text-align: center;
    p {
      border: 1px solid #e6ecf5;
      border-radius: 20px;
      padding: 10px 20px;
      margin-top: 50px;
      a {
        color: #000; } }
    img {
      margin-top: 50px; } } }
.or {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  &:after, &:before {
    content: "";
    display: block;
    background: #e6ecf5;
    width: 40%;
    height: 1px; }
  &:after {
    margin-left: 10px; }
  &:before {
    margin-right: 10px; } }

.login__capctha {
  text-align: center; }

@media screen and (max-width: 900px) {
  .login__registration {
    .login__input-field {
      text-align: center;
      p {
        border: 1px solid #e6ecf5;
        border-radius: 20px;
        padding: 10px 20px;
        margin-top: 30px; }
      img {
        margin-top: 30px; } } } }
@media screen and (max-width: 750px) {
  .login__container {
    flex-wrap: wrap;
    justify-content: center; }
  .login__info, .login__input-window {
    width: 70%;
    margin-bottom: 20px; }
  .login__switch-panel {
    width: 50px; }
  .login__input-window {
    width: 90%;
    margin-top: 50px; } }
@media screen and (max-width: 500px) {
  .login__input-window {
    margin-bottom: 30px;
    height: 700px;
    flex-wrap: wrap; }
  .login__switch-panel {
    width: 100%;
    display: flex;
    height: 50px;
    button {
      width: 50%;
      height: 70px;
      border-bottom: 1px solid #e6ecf5;
      &:first-child {
        border-right: 1px solid #e6ecf5; } } }
  .login__registration {
    height: 100%; }
  .login__input-window-with-captcha {
    height: 840px; } }
@media screen and (max-width: 370px) {
  .login__capctha {
    img {
      width: 90%; } } }
