.dark-theme {
  .settings__margin {
    color: #fff; }
  .settings__main-blocks {
    background: #444;
    box-shadow: 0 4px 16px #222; } }
.white-theme {
  background: #edf2f5;
  .settings__main-blocks {
    background: #fff;
    box-shadow: 0 4px 16px #ccc; } }


.dark {
  button {
    background: #333; }
  .settings__big-round {
    animation: dvij6 0.6s; } }
.white {
  button {
    background: #fff; }
  .settings__big-round {
    animation: dvij5 0.6s; } }

.settings {
  overflow: hidden;
  transition: 0.5s;
  padding-bottom: 25px;
  box-sizing: border-box;
  min-height: 100vh; }
.settings__title {
  margin: 0 auto;
  padding-top: 70px; }
.settings__subtitle {
  text-align: center;
  img {
    width: 20%; } }
.settings__container {
  margin: 0 auto; }



.settings__margin {
  padding: 20px 30px; }


.settings__main-blocks {
  margin-top: 50px;
  border-radius: 20px; }


.settings__checkpoint {
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  transition: 0.5s;
  transform: scale(1);
  border: none; }

.settings__your-theme {
  button {
    position: relative;
    border: 3px solid limegreen;
    img {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 40px;
      margin-top: -20px;
      margin-left: -20px; } } }

.settings__button {
  margin-left: 20px;
  position: relative;
  .settings__big-round {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transition: 0.5s;
    transform: scale(70);
    z-index: -1; } }


@keyframes dvij5 {
  100% {
    z-index: 1;
    background: #fff;
    transform: scale(1); } }
@keyframes dvij6 {
  100% {
    z-index: 1;
    background: #333;
    transform: scale(1); } }


.settings__themes-buttons {
  display: flex;
  align-items: center; }

.settings__language {
  display: flex;
  align-items: center;
  p {
    &:nth-child(2) {
      cursor: pointer;
      box-sizing: border-box;
      margin-left: 20px;
      transition: 0.6s;
      border: 2px solid transparent;
      padding: 10px 5px;
      border-radius: 10px;
      &:hover {

        border: 2px solid #00BFFF; } } } }
