.users {
  padding-bottom: 100px; }

.users__container {
  margin: 0 auto; }

.users__title { // заголовок сверху страницы
  padding-top: 70px;
  margin: 0 auto; }

.users__subtitle { // картинки сверху. подзаголовок
  margin-top: 50px;
  display: flex;
  justify-content: center;
  img {
    display: block;
    width: 40%; } }
.users__paginator {
  margin: 40px 0; }

.users__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.users__card {
  position: relative;
  cursor: pointer;
  margin: 40px 25px 0;
  width: 250px;
  height: 400px;
  perspective: 1000px;
  &:hover {
    .users__card-back {
      transform: rotateY(360deg); }
    .users__card-front {
      transform: rotateY(180deg); } } }

.users__card-front, .users__card-back {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 20px;
  box-shadow: 0 4px 16px #ccc;
  width: 100%;
  height: 100%;
  transition: 1.2s;
  backface-visibility: hidden; }
.users__card-front {
  background: #fff;
  text-align: center; }

.users__card-back {
  background: url('https://avatars.mds.yandex.net/get-pdb/2427227/d79826c0-e9ea-4e54-be94-183efda548cc/s1200') no-repeat;
  background-size:  cover;
  transform: rotateY(180deg); }

.users__action-on-user {
  background: linear-gradient(45deg, rgba(#1E90FF, 0.3), rgba(#FFFF00, 0.3));
  width: 100%;
  border-radius: 20px;
  box-shadow: 0 4px 16px #ccc;
  height: 100%; }
.users__avatar-user {
  img {
    margin: 30px 0 20px;
    width: 150px;
    border-radius: 50%; } }

.normal-button {
  button, p {
    font-size: 14px;
    cursor: pointer;
    border: none;
    box-sizing: border-box;
    width: 100%;
    color: #fff;
    padding: 10px; } }

.users__follow-on-you {
  background: #32CD32;
  margin-top: 60px; }
.users__unfollow-on-you {
  background: #FF1493;
  margin-top: 60px; }


.users__normal-button_first {
  button {
    background: #FFE500;
    margin-top: 300px; } }
.users__normal-button_second {
  button {
    background: #9240D5;
    margin-top: 5px; } }

.users__normal-button_three {
  button {
    background: #B53AD4;
    margin-top: 5px; } }


.users__buttons {
  opacity: 0.9; }

.users__search-form {
  text-align: left;
  width: 35%;
  margin: 0 auto;
  position: relative;
  .field-normal {
    input {
      width: 100%;
      height: 40px;
      padding: 0 0 0 60px;
      box-sizing: border-box; } }

  button {
    border: none;
    border-right: 1px solid #9a9fbf;
    background: transparent;
    padding-right: 35px;
    position: absolute;
    top: 50%;
    margin-top: -15px;
    left: 0;
    width: 45px;
    height: 30px;
    cursor: pointer;
    img {
      margin-left: 15px;
      height: 20px;
      width: 20px; } } }

.dark-theme {
  .users__card-front, .users__card-back {
    box-shadow: 0 4px 16px #222; }
  .users__action-on-user {
    box-shadow: 0 4px 16px #222; }
  .users__card-front {
    background: #555; } }

@media screen and (max-width: 1000px) {
  .users__search-form {
    width: 50%; } }


@media screen and (max-width: 500px) {
  .users__search-form {
    width: 70%; } }

.dark-theme {
  .field-normal {
    input {
      background: transparent;
      color: #fff; } } }

.user__error-search {
  margin-top: 100px;
  text-align: center;
  img {
    margin-bottom: 50px; }
  p {
    font-size: 32px; } }

