.preloader {
  z-index: 4000;
  background: rgba(#fff, 0.9);
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0; }

.preloader__window {
  width: 70px;
  height: 70px;
  position: relative;
  animation: preloader 2s infinite ease-in-out; }

.preloader__line {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: 5px solid #9a9fbf; }

.line1 {
  animation: line1 2s infinite ease-in-out; }
.line2 {
  animation: line2 2s infinite ease-in-out; }
.line3 {
  animation: line3 2s infinite ease-in-out; }
.line4 {
  animation: line4 2s infinite ease-in-out; }

@keyframes line1 {
  0% {
    transform: translate(0); }
  50% {
    transform: translateX(80px);
    border-color: #1E90FF; }
  100% {
    transform: translate(0); } }
@keyframes line2 {
  0% {
    transform: translate(0); }
  50% {
    transform: translateX(-80px);
    border-color: mediumspringgreen; }
  100% {
    transform: translate(0); } }
@keyframes line3 {
  0% {
    transform: translate(0); }
  50% {
    transform: translateY(80px);
    border-color: yellow; }
  100% {
    transform: translate(0); } }
@keyframes line4 {
  0% {
    transform: translate(0); }
  50% {
    transform: translateY(-80px);
    border-color: red; }
  100% {
    transform: translate(0); } }
@keyframes preloader {
  0% {
    transform: rotate(-30deg); }
  50% {
    transform: rotate(-100deg); }
  100% {
    transform: rotate(-200deg); } }

.dark-theme {
  .preloader {
    background: rgba(#000, 0.5); } }
