
* {
  margin: 0;
  padding: 0;
  outline: none; }
p {
  font-size: 14px; }
li {
  list-style: none; }

.main_window {
  background: rgba(#000, 0.1); }
a {
  text-decoration: none; }
.main-window-off {
  display: none; }
.container {
  width: 70%; }
.main-text {
  color: #666666;
  font-weight: 600; }
.additional-text {
  color: #9a9fbf;
  font-weight: 600; }
.main-bg {
  background: #edf2f5;
  margin-left: 80px; }
.title-information {
  font-size: 16px;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 3px solid #edf2f5; }
.title {
  text-align: center;
  width: 40%;
  h1, h3 {
    color: #fff;
    font-weight: normal;
    font-size: 16px; }
  h1 {
    font-size: 40px;
    margin-bottom: 20px; } }

.form {
  textarea {
    box-sizing: border-box;
    width: 100%;
    border-radius: 5px;
    border: none; } }
.field-error__img-error {
  position: absolute;
  top: 50%;
  margin-top: -16px;
  right: 20px; }
.field-error {
  position: relative;
  border: 2px solid red; }
.submit-button {
  button {
    position: relative;
    cursor: pointer;
    border: none;
    color: #fff;
    border-radius: 5px;
    outline: none;
    transition: 0.5s;
    &:hover {
      opacity: 0.8; } } }


.full_error {
  border: 2px solid red;
  border-radius: 20px;
  padding: 10px 20px;
  margin-bottom: 15px; }
@mixin moving-background ($url, $size, $animation, $time) {
  width: 100%;
  background: $url;
  background-size: $size;
  background-position: 0 0;
  animation-name: $animation;
  animation-duration: $time;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-delay: 0s; }

@mixin text-on-moving-bg ($bg, $h) {
  padding-top: 70px;
  background: rgba($bg, 0.9);
  height: $h;
  width: 100%; }

.dialogs__bg-header {
  @include moving-background(url('https://krot.info/uploads/posts/2020-01/1580232873_30-p-foni-s-zadnimi-gorodami-102.jpg') repeat-x, 1000px, moving-background-animate, 30s); }
.dialogs__header {
  @include text-on-moving-bg(#ff7400, 100%); }

.users__bg-header {
  @include moving-background(url('https://krot.info/uploads/posts/2020-01/1580232873_30-p-foni-s-zadnimi-gorodami-102.jpg') repeat-x, 800px, moving-background-animate, 40s); }
.users__header {
  @include text-on-moving-bg(#00BFFF, 100%); }


.friends__bg-header {
  @include moving-background(url('https://krot.info/uploads/posts/2020-01/1580232873_30-p-foni-s-zadnimi-gorodami-102.jpg') repeat-x, 1000px, moving-background-animate, 40s); }
.friends__header {
  @include text-on-moving-bg(#D936C0, 100%); }

.login {
  @include moving-background(url('https://www.tugraz.at/fileadmin/_processed_/a/a/csm_Faces2_by_Lunhammer_tugraz_2be7570e52.jpg') repeat, 600px, moving-background-animate2, 70s); }
.login__bg-header {
  @include text-on-moving-bg(rgb(255, 94, 58), 100%); }

.settings__bg-header {
  @include moving-background(url('https://krot.info/uploads/posts/2020-01/1580232873_30-p-foni-s-zadnimi-gorodami-102.jpg') repeat-x, 1000px, moving-background-animate, 40s); }
.settings__header {
  @include text-on-moving-bg(#34D800, 100%); }

@keyframes moving-background-animate {
  to {
    background-position: -500% 0; } }

@keyframes moving-background-animate2 {
  to {
    background-position: -500% 500%; } }

@media screen and (max-width: 1150px) {
  .title {
    width: 50%;
    h1, h3 {
      font-size: 16px; }
    h1 {
      font-size: 30px; } }
  .container {
    width: 90%; } }
@media screen and (max-width: 800px) {
  .title {
    width: 60%;
    h1, h3 {
      font-size: 12px; }
    h1 {
      font-size: 24px; } } }
@media screen and (max-width: 700px) {
  .main-bg {
    margin-left: 0; }
  .main-app {
    overflow: hidden; } }


.dark-theme {
  .main-bg {
    background: #333; }
  .main-text, .additional-text {
    color: #fff; }
  .title-information {
    border-bottom: 3px solid #333; }
  background: rgba(#000, 0.5); }
