
.dialogs__container { // все сообщения
  margin: 150px auto 0;
  padding-bottom: 50px;
  min-height: 70vh;
  box-sizing: border-box;
  display: flex; }


.dialogs__title { // заголовок сверху страницы
  padding-top: 70px;
  margin: 0 auto; }

.dialogs__subtitle { // картинки сверху. подзаголовок
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 25px;
  img {
    max-width: 200px; } }

.dialogs__interlocutors { // все собеседники
  width: 350px;
  max-height: 700px;
  overflow: hidden;
  &:hover {
     overflow: auto; } }

.dialogs__interlocutor { // каждый собеседник
  display: flex;
  align-items: center;
  width: 100%;
  height: 70px;
  background: #fff;
  cursor: pointer;
  img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 10px; }
  .dialogs__interlocutor-name {
    margin-left: 20px; }
  margin-bottom: 2px;
  &:hover {
    background: #ff7400;
    p {
      color: #fff; } } }


.dialogs__window-messages { // окно текста сообщений
  width: 75%;
  position: relative;
  max-height: 700px; }


.dialogs__window-messages-container { // контейнер окна сообщений
  width: 100%;
  position: absolute;
  bottom: 0; }

.dialogs__all-messages {
  max-height: 500px;
  overflow: hidden;
  padding: 0 25px;
  box-sizing: border-box;
  &:hover {
    overflow: auto; } }
.dialogs__empty-message { // пустое сообщение
  text-align: center;
  margin-bottom: 100px;
  img {
    opacity: 0.1; } }

.dialogs__message { // каждое сообщение
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  img {
    width: 50px;
    height: 50px;
    border-radius: 50%; }
  p {
    margin-left: 10px;
    font-size: 16px;
    word-break: break-all;
    background: #fff;
    padding: 5px 10px;
    border-radius: 5px; } }



.dialogs__text-form {
  padding: 10px 25px 5px;
  background: #e6e6e6; }

.dialogs__form { // текстовое поле
  textarea {
    padding: 20px;
    height: 100px;
    display: block;
    box-sizing: border-box; } }

.dialogs__submit-button { // кнопка отправки
  margin: 10px 0;
  text-align: right;
  button {
    background: #ff7400;
    padding: 10px;
    width: 120px; } }
.dialogs__exit {
  display: none;
  cursor: pointer;
  padding-bottom: 10px;
  margin-bottom: 10px;
  img {
    width: 20px; }
  border-bottom: 2px solid #9a9fbf; }

.active-interlocutor {
  background: #ff7400;
  p {
    color: #fff; } }
@media screen and (max-width: 1150px) {
  .dialogs__subtitle {
    margin-top: 30px;
    text-align: center;
    img {
      max-width: 80%; } }
  .dialogs__title {
    padding-top: 30px; } }

@media screen and (max-width: 700px) {
  .dialogs__container { // все сообщения
    padding-bottom: 150px; }
  .dialogs__interlocutors { // все собеседники
    max-height: 420px; }
  .dialogs__interlocutor { // каждый собеседник
    height: 50px;
    img {
      width: 40px;
      height: 40px;
      margin-left: 5px; }
    .dialogs__interlocutor-name {
      p {
        font-size: 10px; }
      margin-left: 10px; } }
  .dialogs__all-messages {
    max-height: 290px;
    padding: 0 5px; }
  .dialogs__form { // текстовое поле
    textarea {
      padding: 5px 10px;
      height: 50px; } }
  .dialogs__submit-button { // кнопка отправки
    margin: 5px 0;
    text-align: left;
    button {
      background: #ff7400;
      padding: 5px;
      width: 90px; } }
  .dialogs__message { // каждое сообщение
    img {
      width: 20px;
      height: 20px; }
    p {
      margin-left: 5px;
      font-size: 12px; } }
  .dialogs__empty-message { // пустое сообщение
    margin-bottom: 50px;
    img {
      width: 90%; } }
  .dialogs__container { // все сообщения
    margin: 50px auto 0; } }

@media screen and (max-width: 500px) {
  .dialogs__interlocutors { // все собеседники
    max-height: 420px; }
  .dialogs__interlocutor { // каждый собеседник
    height: 60px;
    .dialogs__interlocutor-name {
      p {
        font-size: 12px; }
      margin-left: 15px; } }
  .dialogs__all-messages {
    max-height: 350px; }
  .dialogs__submit-button { // кнопка отправки
    text-align: right;
    button {
      padding: 10px;
      width: 100px; } }
  .dialogs__message { // каждое сообщение
    img {
      width: 30px;
      height: 30px; } }
  .dialogs__window-messages { // окно текста сообщений
    width: 100%; }
  .dialogs__window-messages-container { // контейнер окна сообщений
    position: relative; }
  .dialogs__exit {
    display: block; }
  .dialogs__container { // все сообщения
    justify-content: center; }
  .active-interlocutor {
    background: #fff;
    p {
      color: #000; } } }

.dark-theme {
  .dialogs__interlocutor { // каждый собеседник
    background: #555; }
  .active-interlocutor {
    background: #00BFFF; }
  .dialogs__text-form {
    background: #777; }
  .dialogs__message { // каждое сообщение
    p {
      background: #555;
      color: #fff; } }
  .dialogs__form { // текстовое поле
    textarea {
      background: #555;
      color: #fff; } } }
