.profile__container {
  margin: 0 auto;
  padding: 70px 0 50px; }
.profile__about-me {
  margin-top: 50px; }
.profile__header {
  position: relative; }

.profile__main-blocks {
  box-shadow: 0 4px 16px #ccc;
  background: #fff;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  margin-bottom: 30px; }


.profile__header-bg {
  img {
    width: 100%;
    height: 400px;
    display: block;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px; } }
.profile__user {
  position: absolute;
  bottom: -10%;
  left: 44%; }
.profile__user-avatar {
  img {
    border: 5px solid #fff;
    border-radius: 50%;
    width: 180px;
    height: 180px;
    display: block; } }
.profile__user-settings {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: 0;
  border-radius: 50%;
  transition: 0.9s;
  img {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 37%;
    left: 37%; }
  input {
    cursor: pointer;
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0; }
  &:hover {
    opacity: 1; } }
.profile__links-content {
  padding: 50px 0 30px;
  ul {
    display: flex;
    justify-content: space-around; }
  li {
    font-size: 16px; } }
.profile__user-name {
  a {
    line-height: 0;
    font-size: 24px;
    font-weight: 600;
    color: #000; } }
.profile__main-information {
  display: flex;
  justify-content: space-between;
  img {
    width: 50px; } }
.profile__information-left-side, .profile__information-right-side {
  border-radius: 20px;
  box-sizing: border-box;
  position: relative; }
.profile__information-left-side {
  width: 30%; }
.profile__information-right-side {
  width: 69%; }
.profile__margin {
  padding: 20px 30px; }
.profile__button-edit-profile {
  cursor: pointer;
  width: 35px;
  height: 35px;
  background: transparent;
  border: none;
  img {
    width: 100%;
    display: block;
    height: 100%; }
  position: absolute;
  top: 10px;
  right: 20px; }
.profile__list-about-profile {
  word-break: break-all;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  position: relative;
  .additional-text {
      position: absolute;
      left: 50%; } }
.profile__network {
  .main-text {
    display: flex;
    align-items: center;
    img {
      margin-right: 20px; } } }

.input-style1 {
  input {
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0.02);
    border: none;
    border-bottom: 1px solid #999;
    border-radius: 3px;
    outline: none;
    font-size: 14px;
    transition: 0.3s;
    &:focus {
      background: rgba(0, 0, 0, 0.07);
      border-bottom: 3px solid #FF7400; } } }

.profile__input-style1 {
  input {
    height: 40px;
    width: 100%;
    padding: 5px 10px; } }
//////////////////////////



.profile__post {
  position: relative;
  border-radius: 20px;
  margin-bottom: 30px;
  padding: 40px;
  background: #fff;
  display: flex;
  align-items: center; }
.profile__sender {
  width: 100%;
  display: flex;
  align-items: center;
  margin: 0 40px;
  img {
    border-radius: 50%;
    width: 70px;
    margin-right: 40px; } }
.profile__post-text {
  p {
    word-break: break-all;
    font-style: italic;
    font-size: 18px;
    line-height: 1.5;
    &:first-child {
      font-weight: 400;
      margin-bottom: 10px; } } }
.arrow {
  cursor: pointer;
  img {
    opacity: 0.2;
    width: 25px; } }
.name-with-line {
  &:before {
    content: "";
    display: inline-block;
    height: 2px;
    width: 30px;
    background: red;
    margin: 0 10px 5px 0; } }
.profile__info-number-comment {
  position: absolute;
  bottom: 20px;
  right: 5%; }

.profile__post-form {
  box-shadow: 0 4px 16px #ccc;
  position: relative;
  width: 50%;
  margin: 0 auto; }

.profile__form {
  textarea {
    outline: none;
    border-bottom: 1px solid #FF7400;
    border-radius: 0;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    height: 100px;
    padding: 20px 30px 20px 100px;
    display: block;
    transition: 0.9s;
    &:focus {
      border-bottom: 1px solid #1E90FF; } } }
.profile__submit-button {
  background: #fff;
  padding: 12px 0;
  text-align: center;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  button {
    padding: 10px 20px;
    background: #FF7400; } }

.profile__sender-post {
  position: absolute;
  top: 10px;
  left: 20px;
  img {
    width: 60px;
    border-radius: 50%; } }

@media screen and (max-width: 1800px) {
  .profile__user {
    left: 43%; } }
@media screen and (max-width: 1600px) {
  .profile__user {
    left: 42%; } }
@media screen and (max-width: 1300px) {
  .profile__user {
    left: 42%; }
  .profile__container {
    width: 90%; } }
@media screen and (max-width: 1150px) {
  .profile__user {
    left: 40%; } }
@media screen and (max-width: 1100px) {
  .profile__header-bg {
    img {
      height: 300px; } }
  .profile__user-avatar {
    img {
      width: 130px;
      height: 130px; } }
  .profile__user {
    left: 43%; }
  .profile__user-settings {
    img {
      width: 40px;
      height: 40px; } }
  .profile__title-information {
    font-size: 14px; }
  .profile__button-edit-profile {
    width: 25px;
    height: 25px;
    top: 20px;
    right: 10px; } }
@media screen and (max-width: 1000px) {
  .profile__links-content {
    li {
      font-size: 12px; } }
  .profile__user-name {
    a {
      font-size: 16px; } }
  .profile__main-information {
    justify-content: center;
    flex-wrap: wrap; }
  .profile__information-left-side {
    width: 100%;
    margin-bottom: 30px; }
  .profile__information-right-side {
    width: 100%; }
  .profile__post-text {
    p {
      font-size: 16px;
      line-height: 1.3; } }
  .profile__post-form {
    width: 100%; } }
@media screen and (max-width: 900px) {
  .profile__user {
    left: 42%; } }
@media screen and (max-width: 800px) {
  .profile__user {
    left: 40%; } }
@media screen and (max-width: 700px) {
  .profile__user {
    left: 42%; }
  .profile__header-bg {
    img {
      height: 200px; } }
  .profile__user-avatar {
    img {
      width: 100px;
      height: 100px; } }
  .profile__user-settings {
    img {
      width: 30px;
      height: 30px; } }
  .profile__post {
    padding: 20px; }
  .profile__sender {
    margin: 0 20px;
    img {
      margin-right: 20px; } }
  .profile__post-text {
    p {
      font-size: 14px;
      line-height: 1.3;
      &:first-child {
        margin-bottom: 10px; } } }
  .arrow {
    img {
      opacity: 0.2;
      width: 15px; } }
  .profile__form {
    textarea {
      height: 200px; } }
  .profile__submit-button {
    padding: 10px 0;
    button {
      padding: 7px 17px; } } }
@media screen and (max-width: 550px) {
  .profile__user {
    left: 40%; } }
@media screen and (max-width: 500px) {
  .profile__links-content {
    li {
      text-align: center;
      line-height: 1;
      a {
        font-size: 10px; }
      margin: 0 5px; } }
  .profile__user-name {
    a {
      font-size: 12px; } }
  .profile__user {
    left: 39%; }
  .profile__post {
    padding: 10px; }
  .profile__sender {
    margin: 0 10px;
    img {
      margin-right: 10px; } }
  .profile__post-text {
    p {
      font-size: 12px; } }
  .name-with-line {
    &:before {
      width: 20px;
      margin: 0 10px 3px 0; } }
  .profile__info-number-comment {
    bottom: 10px; }
  .profile__list-about-profile {
    margin-bottom: 30px;
    font-size: 12px;
    p {
      font-size: 12px; }
    .additional-text {
      left: 60%; } }
  .profile__network {
    .main-text {
      img {
        margin-right: 10px; } } }
  .input-style1 {
    input {
      font-size: 12px; } }
  .profile__input-style1 {
    input {
      width: 100px; } }
  .profile__network {
    img {
      width: 30px; } } }
@media screen and (max-width: 400px) {
  .profile__user {
    left: 36%; } }



@media screen and (max-width: 390px) {
  .profile__info-number-comment {
    bottom: 1px;
    left: 5%; }
  .profile__post-text {
    p {
      font-size: 10px; } }
  .profile__list-about-profile {
    font-size: 10px;
    p {
      font-size: 10px; }
    .additional-text {
      left: 60%; } }
  .profile__network {
    .main-text {
      img {
        margin-right: 10px; } } }
  .input-style1 {
    input {
      font-size: 12px; } }
  .profile__input-style1 {
    input {
      width: 80px; } } }
@media screen and (max-width: 350px) {
  .profile__user {
    left: 34%; } }

.dark-theme {
  .profile__main-blocks {
    box-shadow: 0 4px 16px #222;
    background: #555; }
  .profile__submit-button {
    background: #555; }
  .profile__post-form {
    box-shadow: 0 4px 16px #222; }
  .profile__form {
    textarea {
      background: #555;
      color: #fff;
      &::placeholder {
        color: #fff; } } }
  .profile__user-name {
    a {
      color: #fff; } }
  .input-style1 {
    input {
      color: #fff; } } }
