.send-comment {
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  position: relative;
  margin: 30px auto;
  background: #fff;
  width: 40%;
  p {
    margin: 0; }
  textarea {
    display: block;
    width: 100%;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 30px 70px;
    height: 125px;
    outline: none; }
  .button-shared {
    border-top: 1px solid #e6ecf5;
    padding-top: 10px;
    margin: 0;
    button {
      margin: 0 30px 10px 0; } } }
.avatar_post {
  top: 10%;
  position: absolute;
  img {
    margin-left: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50%; } }
